import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SelectItem } from 'primeng/primeng';
import { EntrustManagerWaitingApproveQryBean } from './bean/departmentPendingEscrow.bean';
import { DepartmentPendingEscrowDetailBean } from './bean/departmentPendingEscrowDetail.bean'
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage} from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { CommonHttpService } from '../../../../../common/http/common.http.service';

@Component({
  selector: 'app-department-pending-escrow',
  templateUrl: './department-pending-escrow.component.html',
  styleUrls: ['./department-pending-escrow.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class DepartmentPendingEscrowComponent implements OnInit {
  @Input() inValue: string;  //
  @Output() outValue: EventEmitter<any> = new EventEmitter();  //
  tabMesg: any = tableMessage;// 
  cities: SelectItem[];
  selectedCity: string;
  title1: string = '';
  items: any;
  items2: any;
  entrustDateFrom: any;	//
  entrustDateTo: any;	//
  entrustApplyDateFrom: any;	//
  entrustApplyDateTo: any;	//
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  total: string;//
  msgs: Message[] = [];   //
  checkValueList: Array<any> = [];
  selectedCities: any;
  showModel: string;  //
  display: true; //
  approvedDetailQryInfo: any[];
  entrustStatus: any = [];
  isEntrustAllCust: any = [];
  addUpHeader: any;//dialog 
  lookOver: boolean = false;// dialog
  entrustApplyStatus: string //
  custNo: string;//
  checkValue: any;//
  pageSize:number;
  //
  statusButton: boolean = true;
  peopleList: any; //
  peopleList1: any;
  //
code:any;
  orgId: string;//id
  entrustManagerWaitingApproveQryBean: EntrustManagerWaitingApproveQryBean = new EntrustManagerWaitingApproveQryBean();
  departmentPendingEscrowDetailBean: DepartmentPendingEscrowDetailBean = new DepartmentPendingEscrowDetailBean()
  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public commonHttpService: CommonHttpService,
    public router: Router,
    public commfunc: Commfunc,
    public route: ActivatedRoute
  ) {
    this.codeValues();
    this.entrustStatus=this.code['EntrustStatus'];
    this.isEntrustAllCust=this.code['IsAlloat']
  }
  //
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code
  }
  ngOnInit() {
    this.entrustManagerWaitingApproveQryBean.pageSize = 10;
    this.entrustManagerWaitingApproveQryBean.pageNum = 1;
    this.departmentPendingEscrowDetailBean.pageSize = 10;
    this.departmentPendingEscrowDetailBean.pageNum = 1;
    this.queryBtn();//
    this.queryTeller();
  }

  //
  queryTeller() {
    //queryTellerByCombConditions
    // 
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    
    this.orgId = ch.orgList[0].orgId;
    let param = {
      orgId: this.orgId,
      pageSize: 1000000
    }
    this.commonHttpService.queryTellerByCombConditions(param).subscribe(data => {
      if (data.tellerList) {
        this.peopleList = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
        this.peopleList1 = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
        this.entrustManagerWaitingApproveQryBean.entrustToEmpName = this.peopleList.tellerName;
        this.entrustManagerWaitingApproveQryBean.entrustToEmpNo = this.peopleList.tellerId;
        this.entrustManagerWaitingApproveQryBean.entrustFromEmpName = this.peopleList.tellerName;
        this.entrustManagerWaitingApproveQryBean.entrustFromEmpNo = this.peopleList.tellerId;
      }
    })
  }
  // 
  //
  showData() {
    this.temData = this.entrustApplyDateFrom;
  }
  queryBtn() {
    this.statusButton = false;
    this.entrustManagerWaitingApproveQryBean.custNo = this.route.snapshot.params['custNo'],
    this.entrustManagerWaitingApproveQryBean.entrustToEmpNo = this.route.snapshot.params['entrustToEmpNo'],
    this.entrustManagerWaitingApproveQryBean.nextSqlId = this.route.snapshot.params['nextSqlId'],
    this.entrustManagerWaitingApproveQryBean.apprResult = this.route.snapshot.params['apprResult']

    this.entrustManagerWaitingApproveQryBean.entrustDateFrom = this.commfunc.transDateN(this.entrustDateFrom);
    this.entrustManagerWaitingApproveQryBean.entrustDateTo = this.commfunc.transDateN(this.entrustDateTo);
    this.entrustManagerWaitingApproveQryBean.entrustApplyDateFrom = this.commfunc.transDateN(this.entrustApplyDateFrom);
    this.entrustManagerWaitingApproveQryBean.entrustApplyDateTo = this.commfunc.transDateN(this.entrustApplyDateTo);
    // 
    // let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    // this.entrustManagerWaitingApproveQryBean.entrustFromEmpNo = ch['userId'];
    // this.entrustApplyStatus='1'
    this.entrustManagerWaitingApproveQryBean.entrustApplyStatus = '1'
    // let param = { entrustApplyStatus: '1' }

    this.cusOperationHttpService.entrustManagerQry(this.entrustManagerWaitingApproveQryBean).subscribe(data => {
      if (data.returnCode.code == '1') {
        this.items = data.myEntrustInfo1;
        this.total = data.totalNum;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  // 
  resetBtn() {
    this.entrustManagerWaitingApproveQryBean = new EntrustManagerWaitingApproveQryBean();
  }
  // 
  ratify() {
    // let param = { entrustApplyId: '008', approveResult: '1' };
    // 
    // let tableD=[];
    // this.checkValueList.forEach(item=>{
    //   let obj={};
    //   obj['custNo']=item.custNo;
    //   tableD.push(obj);
    // })
    let params = {
      custNo: this.checkValueList[0].custNo,
      entrustApplyId: this.checkValueList[0].entrustApplyId,
      approveResult: '1'
    }
    //let param = { applyCustApproveInfo:tableD, approveResult: '1' };
    let ob = this.cusOperationHttpService.entrustManagerApprove(params);
    ob.subscribe(data => {
      if (data.returnCode.code == '1') {
        this.queryBtn();
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });

    })
    // this.queryBtn()
    // this.checkValue = ''
  }
  // 
  noratify() {
    // let param = { myEntrustInfo: this.checkValueList, approveResult: '2' };
    let params = {
      custNo: this.checkValueList[0].custNo,
      approveResult: '2'
    }
    let ob = this.cusOperationHttpService.entrustManagerApprove(params);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });

    })
    // this.queryBtn()
    // this.checkValue = ''
  }
  //
  paginate(event) {
    // ;
    //
    this.entrustManagerWaitingApproveQryBean.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.entrustManagerWaitingApproveQryBean.pageNum = currentPage;
    //
    this.queryBtn()
    this.entrustManagerWaitingApproveQryBean.pageNum = 1;
  }

  //
  // detailQry(col) {
  //   //this.cusOperationHttpService.entrustManagerQry(this.entrustManagerWaitingApproveQryBean).subscribe(data => {
  //   ;
  //   let param = {
  //     entrustApplyId: "001",
  //     pageNum: 1,
  //     pageSize: 5,
  //   }
  //   this.cusOperationHttpService.entrustManagerApprovedDetailQry(param).subscribe(data => {
  //     if (data.errorMessage == null) {
  //       this.items2 = data.approvedDetailQryInfo;
  //       this.total = data.totalNum;
  //     } else {
  //       this.msgs = [];
  //       this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
  //     }
  //   }, error => {
  //     this.msgs = [];
  //     this.msgs.push({ severity: 'error', summary: '', detail: '' });

  //   })
  // }
  // 
  responNum(col) {
    this.addUpHeader = '';
    this.lookOver = true;
    col['pageSize'] = this.departmentPendingEscrowDetailBean.pageSize;
    col['pageNum'] = this.departmentPendingEscrowDetailBean.pageNum;
    // this.detailQry(col)
    let param = {
      entrustApplyId: "001",
      pageNum: 1,
      pageSize: 5
    }
    // ;
    let ob = this.cusOperationHttpService.entrustManagerApprovedDetailQry(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items2 = data.approvedDetailQryInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });

    })
  }

  // 360
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }


  //
  change(car, event) {
    // ;
		/**
		 * event = true checkValueList
		 * event = false checkValueList
		 */
    if (event) {
      // 
      this.checkValueList.push(car);
    } else {
      this.checkValueList.splice(this.checkValueList.indexOf(car), 1);
    }
  }
}
