import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { DepartmentMindEscrowBean } from './bean/departmentMindEscrow.bean'
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router } from '@angular/router'
@Component({
  selector: 'app-department-mind-escrow',
  templateUrl: './department-mind-escrow.component.html',
  styleUrls: ['./department-mind-escrow.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class DepartmentMindEscrowComponent implements OnInit {
  cities: SelectItem[];
  selectedCity: string;
  title1: string = '';
  title2: string = '';
  list: any;                      //
  msgs: Message[] = [];           //
  items: any;
  entrustToEmpName: any = [];	 //
  idenType: any = [];	        //
  myCustLabelList: any = [];	//
  BussinessType: any = [];//
  DepartmentDuty: any = [];	//                 
  selectedValue: string;  //
  selectedValues1: string; // 
  selectedValues2: string;//
  total: string;                 //
  selectedCities: string;  // 
  entrustStartDate: any;	//
  entrustEndDate: any;	//
  ch: any = CALENDAR_CN;
  temData: Date = new Date();
  //
  headerTitle: string = "";
  //
  display: boolean = false;
  showModel: any;
  //
  inValue: string;
  //
code:any;
  departmentMindEscrowBean: DepartmentMindEscrowBean = new DepartmentMindEscrowBean();

  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public router: Router,
    public commfunc: Commfunc
  ) {
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.departmentMindEscrowBean.entrustFromEmpName = ch.userName;
    this.codeValues();
    this.entrustToEmpName=this.code['entrustToEmpName'] ;	 //
    this.idenType =this.code['ProdHoldLoan'] ;	        //
    this.myCustLabelList = this.code['myCustLabelList'];	//
    this.BussinessType= this.code['BussinessType'];//
    this.DepartmentDuty =this.code['DepartmentDuty'] ;	// 
  }

  ngOnInit() {
    this.departmentMindEscrowBean.pageSize = 10;
    this.departmentMindEscrowBean.pageNum = 1;
    // 
    this.doQuery()
  }
  //
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code
  }
  //
  doQuery() {
    this.departmentMindEscrowBean.entrustStartDate = this.commfunc.transDateN(this.entrustStartDate);
    this.departmentMindEscrowBean.entrustEndDate = this.commfunc.transDateN(this.entrustEndDate);
    this.cusOperationHttpService.entrustManageCustQryForDeptManager(this.departmentMindEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.myEntrustDetailQryInfo;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  // 
  doReset() {
    this.departmentMindEscrowBean = new DepartmentMindEscrowBean();
    this.selectedValue = "";
    this.selectedValues1 = "";
    this.selectedValues2 = "";
  }

  // 
  confirmBtn() {
    this.cusOperationHttpService.entrustManageCust(this.departmentMindEscrowBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.items = data.myEntrustManageCustFail;
        this.total = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  // 
  cancelBtn() {
    this.selectedCities = ''
  }
  // 360
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }
  //
  showData() {
    this.temData = this.entrustStartDate;
  }
  //
  paginate(event) {
    // ;
    //
    this.departmentMindEscrowBean.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.departmentMindEscrowBean.pageNum = currentPage;
    //
    this.doQuery()
    this.departmentMindEscrowBean.pageNum = 1;
  }

  orgTree() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '6';
  }
  treeCall(param) {
    this.display = false;
    this.departmentMindEscrowBean.entrustToOrgName = param.orgName;
    this.departmentMindEscrowBean.entrustToOrgId = param.orgId
  }
}
